<template>
	<view class="container padding">
		<view class="title flex-row-start">必选</view>
		<view class="item flex-row-start selected">
			智慧管家A保代金券*3（价值500元）
			<image src="../../static/gift_select.png" class='icon'></image>
		</view>
		<view class='chooseable'>
			<view class="title flex-row-start">二选一</view>
			<view 
				class="item flex-row-start" 
				v-for="(item,index) in giftList" 
				:key="index" 
				:class="[item.active?'selected':'']"
				@click="selectGift(item)"
			>
				{{item.name}}
				<image 
					src="../../static/gift_select.png" class='icon' 
					v-if="item.active"
				></image>
			</view>
		</view>
		<view class="rule flex-row-start" @click="goRule">
			<text>查看兑换说明</text>
			<image src="../../static/info_icon.png" class="icon"></image>
		</view>
		<!--  -->
		<view class='btn_wrapper'>
			<view class="sure">
				<button class="cu-btn block lg" @click="submit" :disabled="btnDisabled" :class="[btnDisabled?'':'canClick']">
					提交
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	import { http2Https} from "../../common/tools.js"
	export default {
		data() {
			return {
				giftList:[
					{name:"充电费用（价值500元）",id:1,active:false},
					{name:"蓝谷居民积分（价值500元）",id:2,active:false},
				],
				select:[3],//1：电卡，2：蓝谷券，3：维保（必选）
				btnDisabled:true,
				paramInfo:{
					orderId:'',
					awardIds:''
				},
				giftRuleUrl:'' //兑换说明
			};
		},
		methods:{
			exchangeRule(){
				this.request("/common/param",{key:"award_exchange_desc"}).then((res)=>{
					if(res.data){
						this.giftRuleUrl=http2Https(res.data)
					}
				})
			},
			selectGift(item){
				console.log(item)
				this.giftList.forEach((lett)=>{
					if(lett.id==item.id){ //点的是这个
						lett.active=!lett.active
						if(lett.active){
							this.select.push(lett.id)
						}else{
							if(this.select.indexOf(lett.id)!=-1){
								this.select.splice(this.select.indexOf(lett.id),1)
							}
						}
					}else{ //其他的置位false
						lett.active=false
						if(this.select.indexOf(lett.id)!=-1){
							this.select.splice(this.select.indexOf(lett.id),1)
						}
					}
				})
				console.log(this.select)
				this.btnDisabled=false
			},
			submit(){
				if(this.select.length<2){
					uni.showToast({title:"请在二选一礼物中勾选您的礼物",icon:"none"});return
				}
				this.btnDisabled=true
				this.paramInfo.awardIds=String(this.select)
				this.request("/orderAward/receiveAward",this.paramInfo).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({
							title:res.resultMsg,
							icon:"success",
							success() {
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			goRule(){
				var _obj={
					url:this.giftRuleUrl
				}
				uni.setStorageSync('webViewInfo', _obj);
				this.perfectNavigateTo({
					url:"/pages/webView/webView"
				})
			},
			
		},
		onLoad(params){
			this.paramInfo.orderId=params.orderId
			this.exchangeRule()
		}
	}
</script>

<style lang="stylus">
	.title
		height:44upx;
		font-size:32upx;
		font-weight:600;
		color:rgba(30,30,30,1);
		margin-bottom:45upx
	.item
		position:relative
		width:100%
		font-size:32upx;
		font-weight:bold;
		width:690upx;
		padding:0upx 30upx
		height:112upx;
		background:rgba(255,255,255,1);
		border-radius:16upx;
		border:2upx solid rgba(203,210,219,1);
		&.selected
			border-color:#0075D6
			color:#0075D6
		.icon
			position:absolute
			bottom:-1upx
			right:0upx
			width:64upx
			height:64upx
	.chooseable
		margin-top:60upx
		.item
			&:not(:first-child)
				margin-top:30upx
	.rule
		margin-top:60upx
		height:40upx;
		font-size:28upx;
		font-family:PingFangSC-Semibold,PingFang SC;
		font-weight:600;
		color:rgba(31,35,52,1);
		.icon
			width:28upx
			height:28upx
			margin-left:20upx
	.btn_wrapper
		position:fixed
		background:#FFFFFF
		width:750upx
		bottom:10upx
		left:0upx
		right:0upx
		height:155upx
		display:flex
		align-items center
		justify-content center
		z-index:10
		.lg
			height:112upx
		.sure
			width:690upx
			button
				border-radius:16upx;
				color:#fff
				background:#B2B6C0
				&.canClick
					background:rgba(31,35,52,1);
</style>
